    app.controller('WelcomeController', ['$scope', '$route', '$routeParams', '$location',
        '$q', '$sce', '$timeout', '$window', 'loggedInUser',
        function ($scope, $route, $routeParams, $location,
                  $q, $sce, $timeout, $window, loggedInUser) {

            var AP = window.RA || window.AP;

            init();

            function init() {
                AP.resize();

                $scope.contentTemplate = function() {
                    return $scope.templates['welcome']; // furl
                };

                $scope.contactSupport = function() {
                    var customData = {};
                    TimesheetUtils.screenshot(function (blob, error) {
                        customData.screenshot = blob;
                        if (error) {
                            customData.error = JSON.parse(JSON.stringify(error));
                        }
                        AP.dialog.create({
                            key: 'bitbucket',
                            width: '720px',
                            height: '400px',
                            chrome: true,
                            header: "Contact Support",
                            submitText: "Submit",
                            customData: customData
                        });
                    });
                };

            }
        }
    ]);
